/*
Normalize Ninja Forms fields with default structure
---------------------------------------------*/
/*
 * Ninja Forms - Form Display Structure
 */
.ninja-forms-form-wrap *,
.ninja-forms-form-wrap *:after,
.ninja-forms-form-wrap *:before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
}

.nf-response-msg {
  display: none;
}

.nf-field-container,
#ninja_forms_required_items {
  clear: both;
  position: relative;
  margin-bottom: 25px;
}
.nf-field-container::after,
#ninja_forms_required_items::after {
  clear: both;
  content: "";
  display: block;
}
.nf-field-container.hidden-container, .nf-field-container.hr-container,
#ninja_forms_required_items.hidden-container,
#ninja_forms_required_items.hr-container {
  margin: 0;
  height: auto;
}
.nf-field-container.hidden-container .nf-field-label, .nf-field-container.hr-container .nf-field-label,
#ninja_forms_required_items.hidden-container .nf-field-label,
#ninja_forms_required_items.hr-container .nf-field-label {
  display: none;
}
.nf-field-container.html-container .nf-field-label,
#ninja_forms_required_items.html-container .nf-field-label {
  display: none;
}

.nf-field-label .nf-label-span,
.nf-field-label label {
  font-weight: bold;
  margin: 0;
  padding: 0;
}

.nf-field-element {
  position: relative;
}
.nf-field-element input,
.nf-field-element select,
.nf-field-element textarea {
  margin: 0;
  width: auto;
  height: auto;
  vertical-align: inherit;
}
.nf-field-element textarea {
  height: 200px;
}

.nf-field-description p:last-child {
  margin: 0;
}

.nf-help {
  background: url("../img/help-info.png") no-repeat;
  display: inline-block;
  width: 16px;
  height: 16px;
}

.nf-input-limit {
  font-size: 12px;
  margin-top: 5px;
}

.nf-static {
  padding: 0 10px;
}

.nf-after-field .nf-error-msg {
  margin-right: 10px;
}

/* ---------------------------------
Screen Reader CSS
----------------------------------*/
.nf-sr-only {
  border: 0;
  clip: rect(0, 0, 0, 0);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  opacity: 0;
  white-space: nowrap;
  width: 1px;
}

/* ---------------------------------
Date/Time FIELD
----------------------------------*/
.nf-field-element .time-wrap {
  padding: 1px;
}

/* ---------------------------------
CHECKBOX FIELD
----------------------------------*/
/* ---------------------------------
RADIO & CHECKBOX LISTS
----------------------------------*/
.list-radio-wrap .nf-field-element ul, .list-radio-wrap .nf-field-element li,
.list-checkbox-wrap .nf-field-element ul,
.list-checkbox-wrap .nf-field-element li,
.list-image-wrap .nf-field-element ul,
.list-image-wrap .nf-field-element li {
  list-style: none !important;
  margin: 0 !important;
  padding: 0 !important;
}
.list-radio-wrap .nf-field-element ul input, .list-radio-wrap .nf-field-element li input,
.list-checkbox-wrap .nf-field-element ul input,
.list-checkbox-wrap .nf-field-element li input,
.list-image-wrap .nf-field-element ul input,
.list-image-wrap .nf-field-element li input {
  margin-right: 5px;
}
.list-radio-wrap .nf-field-element li,
.list-checkbox-wrap .nf-field-element li,
.list-image-wrap .nf-field-element li {
  padding-left: 10px;
  margin: 0 0 5px !important;
