  float: left;
  margin-top: 0.25em;
}

.listimage-wrap li {
  width: 100%;
  height: 100%;
  display: inline-grid;
}
.listimage-wrap li label {
  padding: 5px;
  border-width: 2px;
  border-style: solid;
  border-color: transparent;
}
.listimage-wrap li label.nf-checked-label {
  border-width: 2px;
  border-style: solid;
  border-color: #007acc;
  transition: all 0.5s;
}
.listimage-wrap li label img {
  display: block;
}
.listimage-wrap li input:focus + label {
  outline: 2px solid;
}

/* ---------------------------------
PRODUCT FIELD
----------------------------------*/
.product-wrap > div input {
  width: 80px !important;
}
.product-wrap > div .nf-product-field span {
  padding: 0 5px;
}

/* ---------------------------------
STAR RATING
----------------------------------*/
.starrating-wrap .star {
  border-bottom: 0;
}

/* ---------------------------------
HONEYPOT
----------------------------------*/
.nf-form-hp {
  position: absolute;
  visibility: hidden;
}

/* ---------------------------------
SELECT LIST
----------------------------------*/
.list-select-wrap div div {
  display: none;
}

/* ---------------------------------
COLORS
As minimal as possible
----------------------------------*/
.ninja-forms-req-symbol,
.nf-error-msg {
  color: #E80000;
}

.nf-error .ninja-forms-field {
  border: 1px solid #E80000;
}
.nf-error.listimage-wrap .nf-field-element ul {
  border: 1px solid #E80000;
}

.nf-field-submit-error {
  padding: 15px;
}

.ninja-forms-noscript-message {
  background: #f1f1f1;
  border: 4px dashed #ccc;
  color: #333;
  display: block;
  font-size: 20px;
  margin: 20px 0;
  padding: 40px;
  text-align: center;
}

