  width: 100%;
}
.field-wrap > div input[type=checkbox] {
  width: auto;
}
.field-wrap input[type=submit],
.field-wrap input[type=button],
.field-wrap button {
  width: auto;
}
.nf-field-label {
  order: 5;
}

.nf-field-description {
  width: 100%;
  order: 10;
}

.nf-field-element {
  order: 15;
}

/* ---------------------------------
LABEL ABOVE
----------------------------------*/
.label-above .nf-field-label {
  margin-bottom: 10px;
}

/* ---------------------------------
LABEL BELOW
----------------------------------*/
.label-below .nf-field-label {
  margin-top: 10px;
  order: 15;
}
.label-below .nf-field-element {
  order: 5;
}

/* ---------------------------------
LABEL HIDDEN
----------------------------------*/
.label-hidden .field-wrap {
  flex-wrap: wrap;
}
.label-hidden .nf-field-label {
  height: 0;
  margin: 0 !important;
  width: 100%;
  visibility: hidden;
}
.label-hidden .nf-field-description {
  width: 100%;
  order: 20;
}
.label-hidden .nf-field-element {
  width: 100%;
}

/* ---------------------------------
LABEL LEFT
----------------------------------*/
.label-left .field-wrap {
  flex-direction: row;
  flex-wrap: wrap;
}
.label-left .nf-field-label {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-right: 2%;
  text-align: right;
  width: 35%;
  order: 5;
}
.label-left .nf-field-label label {
  width: 100%;
}
.label-left .nf-field-description {
  margin-left: 35%;
}
.label-left .nf-field-element {
  display: inline-block;
  width: 65%;
  order: 10;
}
.label-left .nf-after-field {
  margin-left: 36.5%;
}

/* ---------------------------------
LABEL RIGHT
----------------------------------*/
.label-right .field-wrap {
  flex-direction: row;
}
.label-right .nf-field-label {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-left: 2%;
  width: 35%;
  order: 10;
}
.label-right .nf-field-description {
  margin-right: 35%;
  order: 15;
}
.label-right .nf-field-element {
  display: inline-block;
  width: 65%;
  order: 5;
}
.label-right .checkbox-wrap .nf-field-label {
  width: auto;
}
.label-right .checkbox-wrap .nf-field-element {
  width: 20px;
}
.label-right .nf-after-field {
  width: 65%;
}

/* ---------------------------------
ADJUST SOME LABEL ABOVE & BELOW ISSUES
----------------------------------*/
.label-below .field-wrap,
.label-above .field-wrap {
  flex-direction: column;
  flex-flow: wrap;
}
.label-below .nf-field-element,
.label-above .nf-field-element {
  margin: 0;
  width: 100%;
}
/* ---------------------------------
ADJUST SOME LABEL LEFT & RIGHT ISSUES
----------------------------------*/
.label-left .textarea-wrap .nf-field-label,
.label-left .listradio-wrap .nf-field-label,
.label-left .listcheckbox-wrap .nf-field-label,
.label-right .textarea-wrap .nf-field-label,
.label-right .listradio-wrap .nf-field-label,
.label-right .listcheckbox-wrap .nf-field-label {
  align-items: flex-start;
}
.label-left .textarea-wrap .nf-field-label,
.label-right .textarea-wrap .nf-field-label {
  margin-top: 5px;
}
.label-left .html-wrap,
.label-left .hr-wrap,
.label-right .html-wrap,
.label-right .hr-wrap {
  display: block;
}
.label-left .html-wrap .nf-field-element,
.label-left .hr-wrap .nf-field-element,
.label-right .html-wrap .nf-field-element,
.label-right .hr-wrap .nf-field-element {
  display: block;
  margin: 0;
  width: 100%;
}

/* ---------------------------------
FIELD DESCRIPTION POSITIONS
----------------------------------*/
/*
Styles for Repeater Field fieldsets and buttons
----------------------------------------------*/
.nf-repeater-fieldsets fieldset {
  margin-bottom: 1em;
}

.nf-remove-fieldset {
  color: red;
  font-size: 1.5em;
  background-color: transparent;
  border: thin solid red;
  padding: 0.1em 0.4em;
  float: right;
  margin-top: -1em;
}

.repeater-container .nf-field-description {
  margin-top: 0.5em;
}
.repeater-container .nf-field-description p {
  font-size: 120%;
}